body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #f0f0f0;
  padding-bottom: 3rem;
}

.rotate {
  transform: rotate(90deg)
}

.App {
  text-align: center;
  position: relative;

  .App-header {
    background-image: linear-gradient(#efefef, #dcdcdc);
    height: 150px;
    padding: 20px;
    color: white;
    border-bottom: 3px solid #e82f11;
    margin-bottom: 10px;

    .logo {
      padding-bottom: 15px;

      img {
        width: 50px;
        height: 50px;
      }
    }

    h2 {
      color: #e82f11;
      font-weight: 600;
    }
  }

  #loader {
    display: none;
    width: 100px;
    color: #e82f11;
    position: absolute;
    top: 400px;
    left: calc(50% - 50px)
  }

  h4 {
    font-weight: 600;
    margin-bottom: 20px;
  }

  #canvas {
    border: 5px solid white;
    border-radius: 20px;
  }

  video {
    border: 5px solid white;
    border-radius: 20px;
    object-fit: cover;
  }

  #photo-taken {
    border: 5px solid white;
    border-radius: 20px;
  }

  .custom-btn {
    background-image: linear-gradient(#fd6935, #e52510);
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 600;
  }

  .step4-thankyou {
    font-weight: 600;
    color: #5c5c5c;
  }

  .step4-block {
    background: #dfdfe1;
    border: 4px solid white;
    padding: 20px 10px;
    border-radius: 20px;
    margin: 40px auto;

    p {
      font-weight: 600;
      color: #5c5c5c;
    }

    input {
      outline: none;
      width: 100%;
      border-radius: 5px;
      border-style: solid;
      border-color: #9d9d9d;
      margin-bottom: 20px;
    }

    .input-group-append {
      justify-content: center;

      button {
        margin: 0 10px;
        width: 120px;
      }
    }
  }
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.version {
  position: fixed;
  display: inline-block;
  bottom: 0;
  right: 0;
}

.web-cam {
  margin-top: 82px;
  transform: rotate(-90deg);
}

.btn-mr {
  margin-top: 119px;
}

@media (orientation: portrait) {
  canvas {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  video {
    margin-top: 6rem;
    margin-bottom: 6rem;
  }
}
